
.navBar{
    min-height: auto;
    background: linear-gradient(to right, #051421, #042727 );
}

#collapse{
    border-color: #07ad68;
    border-width: 2px;
}

#navBrand > img{
    object-fit: contain;
    width: 80px;
    height: auto;
    color: #07ad68;
    transition: 00.5s;
}

#navBrand > img:hover{
    width: 90px;
    height: auto;
    transform: scale(1.2);
}

.link{
    color: aliceblue;
}

#navBrand > h2{
    color: #17b875;
    font-weight: 500;
}

.navItem > .MuiSvgIcon-root {
    font-size: 30px;
    margin-left: auto;
    margin-right: auto;
}

.navItem{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100px;
    transition: 0.5s;
}

.link:hover{
    color: #40bb54;
}

/* .navItem:hover > p{
    display: inline-flex;
    cursor: pointer;
    transition: 0.5s;
} */

.navItem > p {
    display: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-left: auto;
    margin-right: auto;
    font-size: small;
}

.navtext > p{
    font-size: 20px;
    margin-right: 20px;
    font-weight: 500;
    cursor: pointer;
    opacity: 0.7;
    color: #40bb54;
}

.navtext > p:hover{
    opacity: 1;
}