
.tabcontent {
    display: flex;
    position: absolute;
    flex-wrap: wrap;
    max-width: 100%;
    height: 100%;
    justify-content: center;
    padding: 20px;
    padding-top: 0;
    overscroll-behavior-x: none;
        -ms-overflow-style: none;
        scrollbar-width: none;
}

.tablecontent::-webkit-scrollbar{
    display: none;
}

#navtab{
    text-align: center;
    justify-content: center;
    border: none;
    font-size: 15px;
    opacity: 0.7;
    display: inline-flexbox;
    color:  #40bb54;
    transition: 0.3s;
    overscroll-behavior-x: scroll;
}

#nav{
    border: none;
    justify-content: center;
    overscroll-behavior-x: scroll;
}


#navtab:hover{
    cursor: pointer;
    font-weight: 700;
    opacity: 1;
    transform: scale(1.2);
}

#navtab.active{
    background: none;
    font-size: 17px;
    font-weight: 700;
    opacity: 1;
}