
.videocard {
    position: relative;
    width: auto;
    height: auto;
    overflow: hidden;
    margin-left: 10px;
    margin-bottom: 0px;
}

.poster{
    position: relative;
    overflow: hidden;
    max-width: 100%;
    height: auto;
}

.poster:before{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(  transparent 75%, #0e0d0d );
    transition: 0.5s;
    z-index: 1;
}

.videocard:hover .poster{
    bottom: 0px;
    transition: 0.5s;
    transform: scale(1.2);
}

.poster img{
    width: 100%;
}

.details{
    position: absolute;
    padding: 20px;
    width: 100%;
    height: 50%;
    bottom: -35px;
    left: 0;
    box-sizing: border-box;
    transition: 0.5s;
    z-index: 2;
}

.videocard:hover .details{
    bottom: 0px;
    transition: 0.5s;
    background: linear-gradient(0deg,  #0e0d0d -30%, transparent);
}

.details > h2{
    color:  #fff;
    margin: 0;
    padding: 0;
    font-size: 25px;
    font-weight: 500;
}

.details span , p {
    font-size: 14px;
    color:  #fff;
}